/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}



/**
 * Basic styling
 */
body {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    background-color: $background-color;
    -webkit-text-size-adjust: 100%;
    -webkit-font-feature-settings: "kern" 1;
    -moz-font-feature-settings: "kern" 1;
    -o-font-feature-settings: "kern" 1;
    font-feature-settings: "kern" 1;
    font-kerning: normal;
}


/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}



/**
 * Images
 */
img {
    max-width: 100%;
    vertical-align: middle;
}



/**
 * Figures
 */
figure {
	padding-left: 5%;
	padding-right: 5%;
	display: block;
	align: center;
}
figure > img {
    display: block;
}

figcaption {
    font-size: $small-font-size;
}



/**
 * Lists
 */
ul, ol {
    margin-left: $spacing-unit;
}

li {
    > ul,
    > ol {
         margin-bottom: 0;
    }
}



/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
    font-family: $header-font-family;
    color: $headings-color;
    font-weight: bold;
    letter-spacing: .05em;
}



/**
 * Links
 */
a {
    color: mix($headings-color,black,90%);
    text-decoration: none;


    &:visited {
        color: darken($brand-color, 5%);
    }

    &:hover {
        color: $text-color;
        text-decoration: none;
        //text-decoration-skip: ink;
        text-shadow: 0 0 5px lighten($headings-color,20%) ;
    }
}



/**
 * Blockquotes
 */
blockquote {
    color: $grey-color;
    border-left: 4px solid $grey-color-light;
    padding-left: $spacing-unit / 2;
    font-size: 18px;
    font-style: italic;

    > :last-child {
        margin-bottom: 0;
    }
}

/**
 * Code formatting
 */
pre,
code {
    font-size: 15px;
    border: 1px solid $grey-color-light;
    border-radius: 2px;
    background-color: $highlight-bg;
}

code {
    padding: 1px 5px;
}

pre {
    padding: 8px 12px;
    overflow-x: auto;

    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}



/**
 * Wrapper
 */
.wrapper {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
    max-width:         calc(#{$content-width} - (#{$spacing-unit} * 2));
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    @extend %clearfix;

    @include media-query($on-laptop) {
        max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
        max-width:         calc(#{$content-width} - (#{$spacing-unit}));
        padding-right: $spacing-unit / 2;
        padding-left: $spacing-unit / 2;
    }
}



/**
 * Clearfix
 */
%clearfix {

    &:after {
        content: "";
        display: table;
        clear: both;
    }
}



/**
 * Icons
 */
.icon {

    > svg {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: middle;

        path {
            fill: $grey-color;
        }
    }
}


/**
 * Change itemize labels
 */
ul {
	list-style: none;
	margin-left: 0;
	padding-left: 0;
}
ul li {
	margin-left: 2.5em;
	text-indent: 0;
}
ul li:before {
	content: "â–¸";
	display: block;
	float: left;
	text-indent: -1.45em;
	width: 0;
	color: $headings-color;
	font-family: $base-font-family;
}

ol {
	list-style: none;
	counter-reset: item;
	margin-left: 0;
	padding-left: 0;
}
ol li {
	margin-left: 2.75em ;
	text-indent: 0em;
}
ol li:before {
	content: counter(item) ".\00a0\00a0" ;
	counter-increment: item;
	display: inline-block;
	float: left;
	text-align: right;
	text-indent: -2.75em;
	width: 0;
	color: $headings-color;
	font-family: $header-font-family;
	font-weight: bold;
}


hr {
	padding: 0;
	color: $grey-color-light;
	background-color: $grey-color-light;
	border-style: none; //solid;
	margin-top: 3ex;
    margin-bottom: 3ex;
    text-align: center;
	background: $background-color;
}
hr:after {
	content: "*      *      *";
	display: inline-block;
	white-space: pre;
	position: relative;
	font-family: $header-font-family;
	font-size: 1.5*$base-font-size;
	color: $headings-color;
}

img,
iframe {
	background: $highlight-bg;
	padding: 5px;
	border: 1px solid $grey-color-light;
	border-radius: 4px;
	margin: 0ex 2em 1ex 0;
}

.picframe {
	background: $highlight-bg;
	padding: 5px;
	border: 1px solid $grey-color-light;
	border-radius: 4px;
	margin: 0ex 2em 1ex 0;
}


img.blend-colors {
	filter: sepia(30%) hue-rotate(-10deg);
	-webkit-filter: sepia(30%) hue-rotate(-10deg);
}

.digression {
	border-left: solid .5ex mix($headings-color,$background-color,50%);
	font-size: $small-font-size;
	padding-left: .5em;
	padding-top: .35ex;
	padding-bottom: .35ex;
	margin-left: 1em;
	margin-right: 1em;
	margin-top: 2ex;
	margin-bottom: 2ex;
}

.digression-title {
	display: inline;
	font-family: $header-font-family;
	font-weight: bold;
	color: $headings-color;
	font-size: $small-font-size;
	padding-right:1em;
	letter-spacing:0.05em;
}



.red-box {
	border-style: solid;
	border-width: 1px;
	padding: 6px;
	border-color: gray;
	border-radius:5px ;
	background-color: rgb(255,240,240) ;
}
