@charset "utf-8";


// @import "fonts" ;

// Our variables
$base-font-family:   texgyrepagella, Palatino, "Palatino Linotype", "Palatino LT STD", "URWPalladioL", "URW Palladio L", serif;
$header-font-family: epigrafica, Optima, "URWClassicoT", "URW Classico", "URW Classico T", sans-serif;
$base-font-size:     20px;
$base-font-weight:   normal;
$small-font-size:    $base-font-size * 0.875;
$base-line-height:   1.5;

$spacing-unit:     30px;

$headings-color:    #902015;
$brand-color:       $headings-color;
$text-color:        #46433A;
$anchor-text-color: $headings-color;
$background-color:  #F1EED9;


//$grey-color:       #828282;
$grey-color:       $text-color;
$grey-color-light: lighten($grey-color, 40%);
$highlight-bg: lighten($background-color, 5%);
$grey-color-dark:  darken($grey-color, 5%);

// Width of the content area
$content-width:    800px;

$on-palm:          600px;
$on-laptop:        800px;



// Use media queries like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}



// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "base",
        "layout",
        "syntax-highlighting"
;
