.post-categories {
    font-size: $small-font-size;
    float: right;
    background-color: $highlight-bg;
    border: 1px solid $grey-color-light;
    border-radius: 5px;
    padding: 3px;
    line-height: 1;
    font-style: normal;
    color: $grey-color;
    margin-right: 1em;
}

.categories-list-wrapper {
	content: block;
	margin: 1em;
	min-height: 22px;
}

.publications-categories-wrapper {
	content: block;
	margin-right: 1em;
	margin-bottom: 1em;
	margin-top: 1em;
	min-height: 22px;
}

.categories-list {
	float:left;
	width: 50%;
}

.paper-by-categories,
.by-categories {
	float: right;
    font-size: $small-font-size;
    text-align: right;
    padding: 3px;
    line-height: 1;
    font-style: normal;
    color: $grey-color;
    margin-bottom: -22px;
    width: 40%;
}


.post-link {
    //display: inline;
    font-size: 24px;
}

.post-back {
	margin-bottom: 3ex;
}


/**
 * Posts
 */
.post-header {
    margin-bottom: $spacing-unit;
}


.post-title {
    font-size: 42px;
    letter-spacing: .05em;
    line-height: 1.15;

    @include media-query($on-laptop) {
        font-size: 36px;
    }
}

.publication-title {
    font-size: 32px;
    letter-spacing: .5px;
    line-height: 1.15;

    @include media-query($on-laptop) {
        font-size: 26px;
    }
}

.publication-content,
.post-content {
    margin-bottom: $spacing-unit;

	h1 {
        font-size: 32px;

        @include media-query($on-laptop) {
            font-size: 28px;
        }
        margin-top: 2ex;
    }

    h2 {
        font-size: 26px;

        @include media-query($on-laptop) {
            font-size: 22px;
        }
        margin-top: 2ex;
    }

    h3 {
        font-size: 22px;

        @include media-query($on-laptop) {
            font-size: 22px;
        }
        margin-top: 2ex;
    }

}
